<template>
	<div id="bookHome">
		<h2 class="title_comp">신규 도서</h2>
		<form action="" @submit.prevent="submitForm">
			<div class="search_area">
				<div class="txt">EzLibrary</div>
				<div class="input_area">
					<input
						type="text"
						v-model="keyword"
						ref="search"
						placeholder="전체 도서 검색"
					/>
					<button type="submit">
						<font-awesome-icon icon="fa-solid fa-magnifying-glass" />
					</button>
				</div>
			</div>
		</form>
		<NewBook></NewBook>
		<ReadingKing></ReadingKing>

		<Modal v-if="isModalShow" @close="isModalShow = false">
			<div slot="header">"{{ keyword }}"이 포함된 검색 결과입니다.</div>
			<div slot="body" class="book_list_wrap">
				<div
					class="book_list"
					v-for="(book, index) in $store.state.arrBookSearch"
					:key="index"
				>
					<div class="img_info">
						<img
							:src="`https://www.ezcampus.kr:444/File/Details/${book.BOOK_UKEY}.jpg?ukey=${book.BOOK_UKEY}`"
							alt=""
						/>
					</div>
					<div class="book_info">
						<div class="name">{{ book.BOOK_TITLE }}</div>

						<div class="auth">{{ book.BOOK_AUTHOR }}</div>
						<div class="publisher">{{ book.BOOK_PUBLISHER }}</div>
						<div class="date">{{ book.BOOK_PUBLISH_DT.split("T")[0] }}</div>
					</div>
				</div>
			</div>
			<div slot="footer"></div>
		</Modal>
	</div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
library.add(faMagnifyingGlass);
import NewBook from "@/components/book/NewBook.vue";
import ReadingKing from "@/components/book/ReadingKing.vue";
import { fetchBookSearch } from "@/api/index";
import Modal from "@/components/common/ModalPopup.vue";
export default {
	data() {
		return {
			keyword: "",
			isModalShow: false,
		};
	},
	components: {
		NewBook,
		ReadingKing,
		Modal,
	},
	methods: {
		async submitForm() {
			if (this.keyword == "") {
				alert("검색어를 입력해주세요");
			} else {
				try {
					const { data } = await fetchBookSearch(this.keyword);
					console.log(data);
					if (data.length == 0) {
						alert("검색된 도서가 없습니다.");
						this.$refs.search.focus();
					} else {
						// 검색 성공
						this.$store.commit("setBookSearch", data);
						this.isModalShow = true;
					}
				} catch (error) {
					console.log(error);
				}
			}
		},
	},
};
</script>

