<template>
	<div class="new_book_swipe">
		<swiper :options="swiperOption">
			<swiper-slide v-for="book in books" :key="book.BOOK_SQ">
				<div class="img_area">
					<img
						:src="`https://www.ezcampus.kr:444/File/Details/${book.BOOK_UKEY}.jpg?ukey=${book.BOOK_UKEY}`"
						alt=""
					/>
				</div>
				<div class="book_info">
					<div class="book_name">{{ book.BOOK_TITLE }}</div>
					<div class="book_auth">{{ book.BOOK_AUTHOR }}</div>
				</div>
			</swiper-slide>
		</swiper>
	</div>
</template>

<script>
import { fetchBookList } from "@/api/index";
import { NewBookSwiper } from "@/mixins/mixins";
export default {
	mixins: [NewBookSwiper],
	computed: {
		books() {
			return this.$store.state.arrNewBook;
		},
	},
	async created() {
		try {
			const response = await fetchBookList();
			this.$store.commit("setNewBook", response.data);
		} catch (error) {
			// console.log(error.response);
		}
	},
};
</script>

